import {apiGet, apiPost} from "../../assets/js/baseApi";

let configBaseApi = "/api/configmgr/";

let getQrcodeApi = function(type){

    return new Promise((resolve, reject) => {
        apiGet(configBaseApi+'qrcode').then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let saveServerConfigApi = function(data){
    return new Promise((resolve, reject) => {
        apiPost(configBaseApi+'serverInfo', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let getAgentStatusApi = function(){

    return new Promise((resolve, reject) => {
        apiGet(configBaseApi+'clientStatus').then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let startAgentApi = function(){

    return new Promise((resolve, reject) => {
        apiPost(configBaseApi+'startclient').then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let stopAgentApi = function(){

    return new Promise((resolve, reject) => {
        apiPost(configBaseApi+'stopclient').then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let saveConfigApi = function(aurl){
    let data = {
        aurl
    }
    return new Promise((resolve, reject) => {
        apiPost(configBaseApi+'saveConfig', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let getAHostname = function(){
    return new Promise((resolve, reject) => {
        apiGet(configBaseApi+ 'astorehost').then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

export {
    getQrcodeApi,
    getAgentStatusApi,
    startAgentApi,
    stopAgentApi,
    saveConfigApi,
    getAHostname,
    saveServerConfigApi
}


