import {apiGet, apiPost, apiDelete, apiPut} from "../../assets/js/baseApi";

let installStatusBaseApi = "/api/installstatusmgr/";

let getInstallStatusApi = function(type){
    let data =  {
        type
    }
    return new Promise((resolve, reject) => {
        apiGet(installStatusBaseApi+'all', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let deleteInstallStatusHistoryApi = function(task){
    return new Promise((resolve, reject) => {
        apiDelete(installStatusBaseApi+task).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let clearStatusApi = function(type){
    let data = {
        type
    }
    return new Promise((resolve, reject) => {
        apiPost(installStatusBaseApi+"clearAll", data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let batchDeleteInstallStatusHistoryApi = function(tasknames){
    let taskJson = JSON.stringify(tasknames);
    let data = {
        tasknames: taskJson
    }

    return new Promise((resolve, reject) => {
        apiPost(installStatusBaseApi+"batchdelete", data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let getOsTypeApi = function(){
    return new Promise((resolve, reject) => {
        apiGet(installStatusBaseApi+'os').then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}



export {
    getInstallStatusApi,
    deleteInstallStatusHistoryApi,
    batchDeleteInstallStatusHistoryApi,
    getOsTypeApi,
    clearStatusApi
}


