<template>
        <!-- <el-radio-group v-model="isCollapse" style="margin-bottom: 20px;"> -->
    <el-header class="nav-header">
        <ul class="nav-left" >
            <li>
                <i class="fa fa-indent collapseIcon pointer" aria-hidden="true" v-if="isCollapse" @click="collapseAsideNavbar()"></i>
                <i class="fa fa-outdent collapseIcon pointer" aria-hidden="true" v-else @click="collapseAsideNavbar()"></i>
            </li>
            <li>
                <!-- {{appTitle}} -->
                <img src="@/assets/imgs/EIDevice.png" alt="">
            </li>
        </ul>
        <ul class="nav-right">
            <li class="status">
                <span>
                    {{$t('header.agentStatus')}}
                </span>
                <el-switch
                v-model="isOnline"
                active-color="#13ce66"
                @change="switchStatus"
                :disabled="isabled">
                </el-switch> 
            </li>
             <li> 
                <el-dropdown trigger="click"  @command="seting">
                    <span class="el-dropdown-link">
                        <i class="fa fa-cog header-config pointer"></i> 
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item command="astore">
                            <span  class="pointer config-item">
                                <i class="fa fa-cog m-r-5" aria-hidden="true"></i> 
                                {{$t('header.astore')}}
                            </span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>  
            </li>
            <li> 
                <el-dropdown trigger="click" >
                    <span class="el-dropdown-link">
                        <i class="fa fa-qrcode header-qrcode pointer"></i> 
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item class="text-center" >
                            <qr-code :value="qrCodeValue" :options="{ width: 260}"　class="block-center" v-if="isShowCode"></qr-code>
                        </el-dropdown-item>
                        <el-dropdown-item class="qrIntroduce" >
                            {{$t('header.edgeOnCodeIntroduce')}}
                            
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>  
            </li>
            <li> 
                <el-dropdown trigger="click" >
                    <span class="el-dropdown-link">
                        <i class="fa fa-user-circle-o header-user pointer"></i> 
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item class="text-center" :disabled="true" >
                            <img src="@/assets/imgs/face_black.png" alt="">
                            <p style="font-size: 23px"> {{username}}</p>
                        </el-dropdown-item>
                        <el-dropdown-item :disabled="true">
                            <b class="w-150 m-r-5">{{$t('header.agentStatus')}}</b>
                            <span class="c-success" v-if="agentStatus == 5">
                                Online
                            </span>
                            <span class="c-darkgray" v-else>
                                Offline
                            </span>
                        </el-dropdown-item>
                        <el-dropdown-item :disabled="true">
                            <b class="w-150 m-r-5">{{$t('header.hostname')}}</b>{{hostname}}
                        </el-dropdown-item>
                        <el-dropdown-item :disabled="true">
                            <b class="w-150 m-r-5">{{$t('header.deviceName')}}</b>{{deviceName}}
                        </el-dropdown-item>
                        <el-dropdown-item :disabled="true">
                            <b class="w-150 m-r-5">{{$t('header.lastAccessed')}}</b>{{logintime}}
                        </el-dropdown-item>

                        <el-dropdown-item class="m-t-10">
                            <el-button type="warning" size="small" style="width: 100%; padding: 10px;" @click="loginout()">
                                <i class="fa fa-sign-out c-white m-r-5" aria-hidden="true"></i>
                                {{$t('header.loginOut')}}
                            </el-button>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>  
            </li>

            <li> 
                <el-dropdown trigger="click" @command="switchLang">
                    <span class="el-dropdown-link">
                        <i class="fa fa-globe pointer"></i> 
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item class="text-center" command="zh">
                            <p　class="pointer"> 中文</p>
                        </el-dropdown-item>
                        <el-dropdown-item  command="en">
                            <p class="pointer">English</p>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>  
            </li>
        </ul>

         <el-dialog :visible.sync="dialogAUrlVisible"  @close="initUrlInfo()">
            <div slot="title" class="dialog-title">
                <i class="fa fa-cog m-r-10" aria-hidden="true"></i> 
                
                {{$t('header.astore')}}
            </div>
            <el-form :model="aForm" :rules="rules" ref="aForm">
                <el-form-item :label="$t('label.aUrl')" :label-width="formLabelWidth" prop="aUrl">
                    <el-input v-model="aForm.aUrl" autocomplete="off" class="max-w-350" :placeholder="$t('header.inputAUrlHolder')"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAUrlVisible = false">
                    {{$t('global.cancel')}}
                </el-button>
                <el-button type="primary" @click="setAUrl()">
                    {{$t('global.confirm')}}
                </el-button>
            </div>
        </el-dialog>
    </el-header>
</template>
<style lang="scss" scoped>
    @import "../../assets/css/colors"; 

    .nav-header{
        background: $lightgray-color;
        border-bottom: 3px solid $androidDM-color;
        display: -webkit-flex;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: space-between;
        .nav-left{
            padding: 0;
            margin: 0;
            display: flex;
            li{ 
                img{
                    width: 130px;
                    margin-top: 0.6rem;
                }
                &:nth-child(1){
                    margin-right: 1.2rem;
                    margin-top: 0.9rem !important;
                    font-size: 1.1rem;
                }
                color:$primary-color;  
            }
        }
        .nav-right{
            padding:0;
            margin:0;
            display: flex;
            li{  
                i{
                    color: $primary-color;
                    font-size:1.5rem;
                }
                margin-left: 2rem;
                width: 1rem;
                color: $primary-color;  
            }

            li.status{
                width: 150px;
                margin-top:2px;
                span{
                    display: inline-block;
                    margin-right: 5px
                }
                div{
                    display: inline-block;
                }
            }
                              
        }
    }
    .qrIntroduce {
        text-align: center;
        color: rgba(0,0,0,0.6);
        font-size: 14px;
        // margin-top: -20px;
    }

    .el-dropdown-menu__item.is-disabled{
        color: #606266;
    }

</style>
<script>

    import {mapState} from 'vuex'
    import {setLang} from '../../lang/lang'
    import handleResponse from '../restfulapi/handleResponse'
    import qrCode from '@chenfengyuan/vue-qrcode';
    import {getQrcodeApi, getAgentStatusApi} from "../restfulapi/configApi"
    import {getSession, setLocal, getLocal, removeLocal, checkLocal, setSession, removeSession} from "../../assets/js/storage"
    import {applicationTitle} from "../../assets/js/constant"
    import {startAgentApi, stopAgentApi, getAHostname, saveConfigApi} from "../restfulapi/configApi"
    import {eventSourceConn, eventSourceClose, handleMsg} from "../restfulapi/eventSourceApi"
    export default{
        name: 'mainHeader',
        data(){
            let validatePwd = (rule,value,callback)=>{
                let reg=new RegExp(/^[0-9A-Za-z]{3,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifySimpleError')))
                }else{callback()};
            };

            let validateServerAddress = (rule,value,callback)=>{
                let reg=new RegExp(/(http|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyServerAddressError')))
                }else{callback()};
            };
            return {
                img:"",
                appTitle: applicationTitle,
                qrCodeValue: "",
                username:'',
                logintime:'',
                allowleft: "fa fa-long-arrow-left",
                allowright: "fa fa-bars",
                formLabelWidth: "200px",
                isShowCode: false,
                agentStatus: 0,
                hostname: "",
                deviceName: "",
                isOnline: false,
                isabled: false,

                dialogAUrlVisible: false,
                aForm: {
                    aUrl: ""
                },
                rules: {
                    aUrl: {required: true, validator: validateServerAddress, trigger: 'blur'},
                },
            }
        },
        components: {
            qrCode,
        },

        props:{
            isCollapse: {
                type: Boolean,
                default: false
            }
        },

        methods:{
            getuserinfo(){
                this.username = getSession("username");
                this.logintime = getSession("logintime");  
            },

            loginout(){
                this.$router.replace('/');
            },
            switchLang(lang){
                if(lang){
                    cookie.setCookie("link_lang", lang, 120);
                    setLang(lang);
                }
            },
            collapseAsideNavbar(){
                this.$emit("collapseAsideNavbar", "")
            },
            getQrcode(){
                getQrcodeApi().then((obj) => {
                    handleResponse(obj, (res) => {
                        if(res.status == "success"){
                            this.qrCodeValue = res.data;
                            this.isShowCode = true;
                        }
                    })
                })
            },

            getAgentStatus(){
                getAgentStatusApi().then((obj) => {
                    handleResponse(obj, (res) => {
                        if(res.status == "success"){
                            let data = JSON.parse(res.data);
                            this.agentStatus = data.status;
                            this.hostname = data.hostname;
                            this.deviceName = data.devicename;
                            if(data.status == 5){
                                this.isOnline = true;
                            }else{
                                this.isOnline = false;
                            }
                        }
                    })
                })
            },
            switchStatus(){
                this.isabled = true;
                if(this.isOnline){
                    startAgentApi().then((obj) => {
                        this.isabled = false;
                        handleResponse(obj, (res) => {
                            if(res.status == "success"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                     this.getAgentStatus();   
                                })
                               
                            }else{
                                this.isOnline = !this.isOnline;
                                 _g.handleError(res);
                            }
                        })
                    })
                }else{
                    stopAgentApi().then((obj) => {
                        this.isabled = false;
                        handleResponse(obj, (res) => {
                            if(res.status == "success"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                    this.getAgentStatus();  
                                })
                            }else{
                                this.isOnline = !this.isOnline;
                                _g.handleError(res);
                            }
                        })
                    })
                }
            },

            doEventSource(){
                eventSourceConn().then((isConnect) => {
                    if(isConnect){
                        handleMsg("REGISTRATION", (type, data) => {
                            if(data){
                                this.getAgentStatus();
                            }
                            
                        }, false);
                        handleMsg("DEREGISTRATION", (type, data) => {
                            if(data){
                                this.getAgentStatus();
                            }
                        
                        }, false);  
                    }
                    
                });
 
            },

            initUrlInfo(){
                let host = getSession("astore-host");
                if(!host){
                    this.aForm.aUrl ="http://";
                }
            },

            seting(val){
                if(val == "astore"){
                    this.getAHostname();
                    this.dialogAUrlVisible = true;
                }
            },

            getAHostname(){
                removeSession("astore-host")
                getAHostname().then((res) => {
                    handleResponse(res, (data) => {
                        if(data.status == "success"){
                            if(data.data.hostname){
                                let url = data.data.hostname;
                                this.aForm.aUrl = url;
                                setSession("astore-host", url);
                                this.getAgentStatus();
                            }
                        }
                    })
                })
            },

            setAUrl(){
                this.$refs.aForm.validate((valid) => {
                    if(valid) {
                        saveConfigApi(this.aForm.aUrl).then((res) => {
                            handleResponse(res, (data) => {
                                if(data.status == "success"){
                                    let aurl = this.aForm.aUrl
                                    this.dialogAUrlVisible = false;
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        setSession("astore-host", aurl);
                                        store.dispatch('configStatus', aurl);
                                    })
                                }else{
                                    _g.handleError(data)
                                }
                            })
                        })
                        
                    }
                })
                
            }
        },
        created(){
            this.getuserinfo();
            this.getQrcode();
            this.getAHostname();
            this.getAgentStatus();
            let host = getSession("astore-host");
            if(!host){
                this.aForm.aUrl ="http://"
            }
        },

        mounted(){
            this.doEventSource();
        }
    }
</script>




