import {apiGet, apiPost, apiDelete, apiPut} from "../../assets/js/baseApi";
import {dockerType, winExeType} from "../../assets/js/constant"

let dockerComposeBaseApi = "/api/dockercomposemgr/";
let getDockerInfoApi = function(){
    return new Promise((resolve, reject) => {
        apiGet(dockerComposeBaseApi+'getInfo').then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let installDockerApi = function({taskname, tasktype, projectname,  version, downloadurl, type}){
    taskname= dockerType+ new Date().getTime();
    let data = {
        taskname,
        tasktype,
        projectname,
        version,
        downloadurl,
        type
    }
    return new Promise((resolve, reject) => {
        apiPost(dockerComposeBaseApi+'install', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let removeDockerApi = function({taskname, tasktype, projectname,  version, downloadurl, type}){
    let data = {
        taskname,
        tasktype,
        projectname,
        version,
        downloadurl,
        type
    }

    return new Promise((resolve, reject) => {
        apiPost(dockerComposeBaseApi+'remove', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let getRepoDockerApi = function(){
    return new Promise((resolve, reject) => {
        apiGet(dockerComposeBaseApi+'repoApps').then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let getYmlContentApi = function(address){
    let data = {
        address
    }
    return new Promise((resolve, reject) => {
        apiGet(dockerComposeBaseApi+'repoYmlContent', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

export {
    getDockerInfoApi,
    installDockerApi,
    removeDockerApi,
    getRepoDockerApi,
    getYmlContentApi
}


