<template>
    <div v-show="isSingleMode" class="p-r">
        <div class="search" v-show="isShowSearchInput"> 
            <el-input size="small" class="w-250 m-l-10" v-model="keywords" :placeholder="$t('winPkgControl.keywordHolder')" >  
            </el-input>
            <i class="fa fa-search search-icon" aria-hidden="true"></i>
        </div>
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane name="winMarket">
                <span slot="label">
                    <i class="fa fa-sellsy m-r-5" aria-hidden="listLoading"></i>
                    {{$t('winPkgControl.exePkgMarket')}}
                </span>
                <el-table
                    :data="exeTableData.filter(data => !keywords || data.projectname.toLowerCase().includes(keywords.toLowerCase()))"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading"
                    :header-cell-style="{background:'#fafafa',color:'#606266'}"
                    >

                    <el-table-column
                    label=""
                    width="100"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img :src="getSrc(scope.row)" :onerror="defaultImg" alt="no img" style="width:52px"/>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="projectname"
                    :label="$t('winPkgControl.projectName')"
                    min-width="120"
                    sortable
                    >
                    </el-table-column>
                    <el-table-column
                    prop="version"
                    :label="$t('winPkgControl.version')"
                    min-width="120"
                    sortable>
                    </el-table-column>

                    <el-table-column
                    prop="type"
                    :label="$t('winPkgControl.action')"
                    min-width="120"
                    :filters="[{ text: $t('winPkgControl.uninstall'), value: 'uninstall' }, { text: $t('winPkgControl.install'), value: 'install' }, { text: $t('winPkgControl.upgrade'), value: 'upgrade' }]"
                    :filter-method="filterType"
                    filter-placement="bottom-end"
                    >
                        <template slot-scope="scope">
                            <div>
                                <span>
                                    <el-button-group>
                                        <el-button 
                                        size="small" 
                                        type="danger" 
                                        @click="appAction('uninstallExe', scope.row)"
                                        v-if="scope.row.type === 'uninstall'"
                                        :disabled="scope.row.type === 'uninstall'&&!scope.row.tool"
                                        >
                                            {{$t('winPkgControl.uninstall')}}
                                        </el-button>
                                        <el-button 
                                        size="small" 
                                        type="warning" 
                                        @click="appAction('upgradeExe', scope.row)" 
                                        v-if="scope.row.type === 'upgrade'"
                                        >
                                            {{$t('winPkgControl.upgrade')}}
                                        </el-button>
                                        <el-button 
                                        size="small" 
                                        type="primary" 
                                        @click="appAction('installExe', scope.row)" 
                                        v-if="scope.row.type === 'install'"
                                        >
                                            {{$t('winPkgControl.install')}}
                                        </el-button>
                                    </el-button-group>
                                </span>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
             <el-tab-pane name="zipMarket">
                <span slot="label">
                    <i class="fa fa-sellsy m-r-5" aria-hidden="listLoading"></i>
                    {{$t('winPkgControl.compressedPkgMarket')}}
                </span>
                <el-table
                    :data="pkgTableData.filter(data => (!keywords || data.projectname.toLowerCase().includes(keywords.toLowerCase())))"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading"
                    :header-cell-style="{background:'#fafafa',color:'#606266'}"
                    >

                    <el-table-column
                    label=""
                    width="100"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img src="@/assets/imgs/package.png"  alt="no img" style="width:48px" />
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="filename"
                    :label="$t('linuxPkgControl.fileName')"
                    min-width="120"
                    >
                    </el-table-column>
                    <el-table-column
                    prop="projectname"
                    :label="$t('winPkgControl.projectName')"
                    min-width="120"
                    sortable
                    >
                    </el-table-column>
                    <el-table-column
                    prop="version"
                    :label="$t('winPkgControl.version')"
                    min-width="120"
                    sortable>
                    </el-table-column>

                    <el-table-column
                    prop="type"
                    :label="$t('winPkgControl.action')"
                    min-width="120"
                    :filters="[{ text: $t('winPkgControl.uninstall'), value: 'uninstall' }, { text: $t('winPkgControl.install'), value: 'install' }, { text: $t('winPkgControl.upgrade'), value: 'upgrade' }]"
                    :filter-method="filterType"
                    filter-placement="bottom-end"
                    >
                        <template slot-scope="scope">
                            <div>
                                <span>
                                    <el-button-group>
                                        <el-button 
                                        size="small" 
                                        type="danger" 
                                        @click="appAction('uninstallPkg', scope.row)"
                                        v-if="scope.row.type === 'uninstall'"
                                        >
                                            {{$t('winPkgControl.uninstall')}}
                                        </el-button>
                                        <el-button 
                                        size="small" 
                                        type="warning" 
                                        @click="appAction('upgradePkg', scope.row)" 
                                        v-if="scope.row.type === 'upgrade'"
                                        >
                                            {{$t('winPkgControl.upgrade')}}
                                        </el-button>
                                        <el-button 
                                        size="small" 
                                        type="primary" 
                                        @click="appAction('installPkg', scope.row)" 
                                        v-if="scope.row.type === 'install'"
                                        >
                                            {{$t('winPkgControl.install')}}
                                        </el-button>
                                    </el-button-group>
                                </span>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
            <el-tab-pane name="pkgStatusHistory">
                <span slot="label">
                    <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                    {{$t('winPkgControl.winManagerHistory')}}
                </span>
                <pkg-history
                ref="pkgHistory"
                @isFinished="isFinished"
                >
                </pkg-history>
            </el-tab-pane>
        </el-tabs>
    </div>
    
</template>


<script>
    import handleResponse from '@/components/restfulapi/handleResponse';
    // import {getRepoWinPkgsApi} from '../../../components/restfulapi/getRepoWinPackageApi';
    import { 
        getExeInfoApi,
        getPkgInfoApi,
        installExeApi,
        removeExeApi,
        upgradeExeApi,
        getRepoWinPkgsApi,
        getRepoWinPkgsByTypeApi} from '../../../components/restfulapi/winExeApi';

    import { 
        installLinuxPkgApi,
        removeLinuxPkgApi,
        upgradeLinuxPkgApi} from '../../../components/restfulapi/linuxPkgApi';
    import {getSession, checkSession, setSession } from '../../../assets/js/storage';
    import pkgHistory from "./history"
    import {dockerType, winExeType, winPkgExe, winPkgZip} from "../../../assets/js/constant";
    import {returnDownloadServerUrlApi, getDownloadServerUrlApi} from "../../restfulapi/serverConfigApi"
    import {mapState} from 'vuex'
    export default{
        name: 'singlewinPkgControl',
        data(){
            return {
                activeName: "winMarket",
                pkgTableData: [],
                exeTableData: [],
                defaultImg: 'this.src="' + 'static/imgs/default_windows.png' + '"',

                //card
                listLoading: false,
                keywords: '',
                zipKeywords: ""
            }
        },

        props: {
            selectedAgentId: {
                type: String,
                default: ""
            },
            isSingleMode: {
                type: Boolean,
                default: true
            },
            //selectedAgent is offline
            isOffline: {
                type: Boolean,
                default: false
            }
        },
        components: {
            pkgHistory,
        },

        methods:{
            getSensorStatus(){
               
                this.listLoading = true;
                getExeInfoApi().then((obj) => {
                     let pkgManagementInfoArray = [];
                    let devicePkgArray = [];
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "success"){
                            let packageinfo = "";
                            if(res.data){
                                packageinfo = JSON.parse(res.data);
                            }

                            let allPkgData = [];
                            if(packageinfo){
                                allPkgData = packageinfo.data;
                            }
                            if(Object.prototype.toString.call(allPkgData) === "[object Array]" && allPkgData.length > 0){
                                allPkgData.forEach((val) => {
                                    let uninstallObj = {
                                        type: 'uninstall',
                                        filename: "",
                                        projectname: val.projectname,
                                        tool: "",
                                        version: val.version,
                                        address: "",
                                    };
                                    pkgManagementInfoArray.push(uninstallObj);
                                    devicePkgArray.push(uninstallObj);  
                                })
                            }else{
                                console.error("get  win exe in agent error");
                            }
                            this.getRepoPkgs(pkgManagementInfoArray, devicePkgArray, winPkgExe);  
                        }
                        
                          
                    })   
                })

                getPkgInfoApi().then((obj) => {
                     let pkgManagementInfoArray = [];
                    let devicePkgArray = [];
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "success"){
                            let packageinfo = "", allPkgData = [];
                            if(res.data){
                                allPkgData = JSON.parse(res.data);
                            }
                            if(Object.prototype.toString.call(allPkgData) === "[object Array]" && allPkgData.length > 0){
                                allPkgData.forEach((val) => {
                                    let uninstallObj = {
                                        type: 'uninstall',
                                        filename: val.pkgname,
                                        projectname: val.appname,
                                        tool: "",
                                        version: val.version,
                                        address: "",
                                    };
                                    pkgManagementInfoArray.push(uninstallObj);
                                    devicePkgArray.push(uninstallObj);  
                                })
                            }
                            this.getRepoPkgs(pkgManagementInfoArray, devicePkgArray, winPkgZip);  
                        }   
                    })   
                })
            },
            // Deletes an object of a particular value in array
            removeObjInArray(originData, rem_pkg_val, version){
                let res = false;
                if(Object.prototype.toString.call(originData) === "[object Array]"){
                    
                    originData.forEach((obj_val, index) => {
                        if(obj_val.projectname === rem_pkg_val&& obj_val.version === version){
                            originData.splice(index ,1)
                            res = true;
                        }
                    })
                }else{
                     console.error("data is not array")
                     
                }
                return res;
            },
            //get repo apps
            getRepoPkgs(pkgManagementInfoArray, devicePkgArray, type){
                if(
                    Object.prototype.toString.call(pkgManagementInfoArray) != "[object Array]" ||
                    Object.prototype.toString.call(devicePkgArray) != "[object Array]"
                ){
                    console.error("getRepoPkgs　parameter is not array")
                    return;
                }
                let token;
                let installManagementInfo = {};
                let upgradeManagementInfo = {};
                this.listLoading = true;
                getRepoWinPkgsByTypeApi(type).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            let installappopt = "";
                            let upgradeappopt = "";
                            let lastVersionCode = [];
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                res.data.forEach((val) => {
                                    let version = val.versionName != null ? val.versionName:"";
                                    installManagementInfo = {
                                        type : "install", 
                                        filename: val.filename, 
                                        projectname: val.projectname, 
                                        tool: val.tool,
                                        version: val.version, 
                                        address: val.address
                                    };
                                    pkgManagementInfoArray.push(installManagementInfo);
                         
                                    devicePkgArray.forEach((deviceapp_val, index) => {
                                    if(((type == winPkgExe&& deviceapp_val.projectname.indexOf(val.projectname) != -1)||
                                        (type == winPkgZip&& deviceapp_val.projectname == val.projectname))){
                                            // pop latest app info
                                            pkgManagementInfoArray.pop();
                                            devicePkgArray[index].address = val.address;
                                            devicePkgArray[index].tool=val.tool;
                                            if(this.removeObjInArray(pkgManagementInfoArray, deviceapp_val.projectname, deviceapp_val.version)){
                                                pkgManagementInfoArray.push(deviceapp_val)
                                            };
                                            if (_g.cpr_version(val.version, deviceapp_val.version)){  
                                                upgradeManagementInfo = {
                                                    type : "upgrade", 
                                                    filename: val.filename, 
                                                    projectname: val.projectname,
                                                    tool: val.tool,
                                                    productname: deviceapp_val.projectname, 
                                                    version: val.version, 
                                                    address: val.address,
                                                };
                                                    //push update app 
                                                pkgManagementInfoArray.push(upgradeManagementInfo);     
                                                
                                            }
                                            
                                        }
                                    })
                                })
                            }
                            if(type == winPkgExe){
                                this.exeTableData = pkgManagementInfoArray.reverse();
                            }
                            if(type == winPkgZip){
                                this.pkgTableData = pkgManagementInfoArray.reverse();
                            }
                        }
                       
                    })
                    
                })
            },

            actionAppSensor(tasktype, appdata){
                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("winPkgControl."+tasktype), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                    if (willfunc) {
                        _g.openGlobalLoading();
                        if(tasktype == "installExe"){
                            installExeApi(appdata).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status == "success"){
                                        this.activeName = "pkgStatusHistory";
                                        this.$refs.pkgHistory.intervalGetPkgStatusHistory();
                                    }else{
                                        _g.handleError(res)
                                    }
                                })
                            })
                        }else if(tasktype == "upgradeExe"){
                            upgradeExeApi(appdata).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status == "success"){
                                        this.activeName = "pkgStatusHistory";
                                        this.$refs.pkgHistory.intervalGetPkgStatusHistory();
                                    }else{
                                        _g.handleError(res)
                                    }
                                })
                            })
                        }else if(tasktype == "uninstallExe"){
                            removeExeApi(appdata).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status == "success"){
                                        this.activeName = "pkgStatusHistory";
                                        this.$refs.pkgHistory.intervalGetPkgStatusHistory();
                                    }else{
                                        _g.handleError(res)
                                    }
                                })
                            })
                        }if(tasktype == "installPkg"){
                            installLinuxPkgApi(appdata).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status == "success"){
                                        this.activeName = "pkgStatusHistory";
                                        this.$refs.pkgHistory.intervalGetPkgStatusHistory();
                                    }else{
                                        _g.handleError(res)
                                    }
                                })
                            })
                        }else if(tasktype == "upgradePkg"){
                            upgradeLinuxPkgApi(appdata).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status == "success"){
                                        this.activeName = "pkgStatusHistory";
                                        this.$refs.pkgHistory.intervalGetPkgStatusHistory();
                                    }else{
                                        _g.handleError(res)
                                    }
                                })
                            })
                        }else if(tasktype == "uninstallPkg"){
                            removeLinuxPkgApi(appdata).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status == "success"){
                                        this.activeName = "pkgStatusHistory";
                                        this.$refs.pkgHistory.intervalGetPkgStatusHistory();
                                    }else{
                                        _g.handleError(res)
                                    }
                                })
                            })
                        }
                        
                    }
                })
            },

            appAction(tasktype, selectedAppData){
                getDownloadServerUrlApi().then((downloadUrl) => {
                    let setsensorval, version, projectname;
                    if(tasktype === "upgradeExe"){
                        setsensorval = selectedAppData.address + "/" +  selectedAppData.filename;
                        projectname = selectedAppData.productname;
                    }else{
                        setsensorval = selectedAppData.address + "/" +  selectedAppData.filename;
                        projectname = selectedAppData.projectname;
                    }
                    let taskname = winExeType+ (new Date()).getTime();
                    if(setsensorval.indexOf("s3") == 0){
                        setsensorval = setsensorval;
                    }else{
                        setsensorval = downloadUrl+ setsensorval;
                    }
                    let pkgdata = {
                        taskname,
                        filename: selectedAppData.filename,
                        projectname: projectname,
                        tool: selectedAppData.tool,
                        version: selectedAppData.version,
                        tasktype: tasktype, 
                        type: winExeType,
                        downloadurl: setsensorval,
                    }
                    this.actionAppSensor(tasktype, pkgdata)
                })
                
            },

            getSrc(appData){
                let downloadUrl = returnDownloadServerUrlApi();
                return downloadUrl + appData.address+"/icon.ico";
            },
            filterType(value, row){
                return row.type === value;
            },

            initData(){
                this.pkgTableData = [];
            },
            handleClick(tab){
                if(tab.name === "pkgStatusHistory"){
                    this.$emit("isDisabled", false)
                }else{
                    this.$emit("isDisabled", true)
                }
            },
            isFinished(){
                this.getSensorStatus();
            }
        },
        created() {
            this.getSensorStatus();
        },
        computed: {
            isShowSearchInput(){
                if(this.activeName === "winMarket"|| this.activeName === "zipMarket"){
                    return true;
                }else{
                    return false;
                }
            },
            
            ...mapState({
                configStatus: "configStatus"
            }),  
        },

        watch:{
            configStatus(){
                this.initData();
                this.getSensorStatus();
            }
        },
    }
</script>

<style lang="scss" scoped>
    .noData{
        margin-top: 30px;
        p{
            img{
                width: 27px;
                vertical-align: middle;
            }
            margin-bottom: 20px;
            color: #bfbfbf;
            font-size: 21px;
        }
    }

    .search{
        position: absolute;
        top: 3px;
        right: 15px;
        width: 230px;
        z-index: 1001;
        .search-icon{
            position: absolute;
            top: 6px;
            right: 1px;
            color: #ddd;
            z-index: 10001;
        }
    }
</style>



