<template>
    <div class="wrapper" >

        <home-header :isCollapse="isCollapse" @collapseAsideNavbar="collapse"></home-header>

        <div class="body">
            <div class="menu">
                <el-menu 
                    :default-active="activeItem" 
                    background-color="#222d32"
                    text-color="#ddd"
                    active-text-color="#ffbf00"
                    class="el-menu-vertical"  
                    :unique-opened="isUnique"
                    :collapse="isCollapse">
                    <router-link to="/main/control/dockercontrol" v-if="isLinux">
                        <el-menu-item index="dockerControl"> 
                            <svg t="1598321130554" class="icon m-r-9" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4287" width="19" height="19">
                                <path d="M210.488889 733.866667c-28.444444 0-56.888889-22.755556-56.888889-51.2s22.755556-51.2 56.888889-51.2c28.444444 0 56.888889 22.755556 56.888889 51.2s-28.444444 51.2-56.888889 51.2z m671.288889-284.444445c-5.688889-39.822222-34.133333-73.955556-62.577778-102.4l-11.377778-11.377778-11.377778 11.377778c-22.755556 22.755556-28.444444 62.577778-28.444444 96.711111 0 22.755556 11.377778 45.511111 22.755556 68.266667l-34.133334 17.066667c-22.755556 5.688889-45.511111 11.377778-68.266666 11.377777H11.377778v17.066667c-5.688889 51.2 5.688889 102.4 22.755555 147.911111l11.377778 17.066667c62.577778 102.4 176.355556 153.6 295.822222 153.6 233.244444 0 426.666667-102.4 517.688889-324.266667 56.888889 0 119.466667-11.377778 147.911111-68.266666l5.688889-11.377778-11.377778-5.688889c-34.133333-22.755556-79.644444-28.444444-119.466666-17.066667z m-335.644445-39.822222H443.733333V512h102.4V409.6z m0-130.844444H443.733333v102.4h102.4V278.755556z m0-130.844445H443.733333v102.4h102.4V147.911111z m125.155556 261.688889H568.888889V512h102.4V409.6z m-375.466667 0H193.422222V512h102.4V409.6z m125.155556 0H318.577778V512h102.4V409.6zM170.666667 409.6H68.266667V512H170.666667V409.6z m250.311111-130.844444H318.577778v102.4h102.4V278.755556z m-125.155556 0H193.422222v102.4h102.4V278.755556z" :fill="dockerIconColor" p-id="4288"></path>
                            </svg>
                            <span slot="title">{{$t('home.docker')}}</span>
                                                                
                        </el-menu-item>
                    </router-link>
                    <router-link to="/main/control/linuxcontrol" v-if="isLinux">
                        <el-menu-item index="linuxControl"> 
                            <i class="fa fa-linux m-r-14" ></i>
                            <span slot="title">{{$t('home.linuxPkg')}}</span>
                                                                
                        </el-menu-item>
                    </router-link>
                    <router-link to="/main/control/windowscontrol"  v-if="!isLinux">
                        <el-menu-item index="windowsControl"> 
                            <i class="fa fa-windows m-r-14" ></i>
                            <span slot="title">{{$t('home.winPackage')}}</span>
                            
                        </el-menu-item>
                    </router-link>
                </el-menu>
            </div>
            
            <div class="content" >
                <div  class="cf">
                    <transition name="fade" mode="out-in">
                        <!-- <keep-alive> -->
                        <router-view></router-view>
                        <!-- </keep-alive> -->
                    </transition>
                </div>
            </div>
            
        </div> 
        <home-footer></home-footer>  
    </div>

</template>



<style lang='scss' scoped>
    @import "../assets/css/colors";
    $nav-icon-color : $primary-color; 
    $header-height : 70px;
    $aside-width : 210px;

    .wrapper{
        display: -webkit-flex;
        display: flex;
        height: 100vh;
        flex-flow: column nowrap;
        // overflow: hidden;    
        .body{
            display: -webkit-flex;
            flex: 1;
            height: 100%;
            display: flex;
            flex-flow: row nowrap;
            .menu{
                height: 100%; 
                overflow-y: auto;
                overflow-x: hidden;
                .el-menu-vertical:not(.el-menu--collapse) {
                    height: 100%;
                    width: $aside-width;
                    box-sizing: border-box;
                    i {
                        display: inline-block;                    
                    }

                    .el-menu-item-group, .el-menu-item-group li{
                        background-color: #181f23 !important;
                    }
                    .is-active{
                        background-color: rgb(27,36,40) !important;
                    }
                }
                .securityicon{
                    margin-left: -2px;
                }
                .el-menu--collapse{
                    height: 100%;
                }
            }
            .content{
                flex:1;
                margin-right: 0;
                height: 100%;
                overflow-y: auto;
                overflow-x: hidden;
                .fade-enter-actice, .fade-leave-active{
                    transition: opacity .5s
                } 
                .fade-enter, .fade-leave-active{
                    opacity: 0;
                }
            }
        }

        
    }

    
</style>

<script>
    import {mapState} from 'vuex'
    import homeHeader from './main/header'
    import homeFooter from './main/footer'
    import {linuxOs, windowsOs} from '../assets/js/constant'
    import { getLocal } from '../assets/js/storage'
    import {getOsTypeApi} from "./restfulapi/installStatusApi"

    export default {
        name: 'home',
        data() {
            return {
                defaultIconColor:"#909399",
                activeIconColor: "#ffbf00",
                isCollapse: false,
                activeItem: 'dockerControl',
                isUnique: true,

                isLinux: true,
            };
        },
        methods: {
            collapse(){
                this.isCollapse = this.isCollapse? false: true;
            },

            getOs(){
                getOsTypeApi().then((res) => {
                    handleResponse(res, (res) => {
                        if(res.status == "success"){
                            let os = res.data;
                            if(osType == windowsOs){
                                this.isLinux = false;
                                this.activeItem = "windowsControl"
                            }else{
                                this.isLinux = true;
                            }
                        }
                    })
                })
            }
        },

        created(){
            this.activeItem = this.$route.meta.menuname;
            let osType = getLocal("agent-os");
            if(osType == windowsOs){
                this.isLinux = false;
                this.activeItem = "windowsControl"
            }else{
                this.isLinux = true;
            }

        },


        watch: {
            $route(to, from){
                this.activeItem = to.meta.menuname;
                
            },
        },
        computed: {
            dockerIconColor(){
                return this.activeItem == "dockerControl"? this.activeIconColor: this.defaultIconColor 
            }
        },
        
        
        
        components:{
            homeHeader,
            homeFooter
        },
    }
</script>


