import {getLocal} from "../assets/js/storage"
import login from '@/components/account/login'
import dockerControl from "@/components/control/dockercontrol"
import windowsControl from "@/components/control/windowscontrol"
import {linuxOs, windowsOs} from '../assets/js/constant'
import linuxControl from "../components/control/linuxcontrol"


import main from '@/components/home'

let childRoute = [
    {
        path: 'control/dockercontrol',
        component: dockerControl,
        name: 'dockerControl',
        beforeEnter: (to, from, next) => {
            let osType = getLocal("agent-os");
            console.log(osType);
            if(osType == windowsOs){
                next("/main/control/windowscontrol")
            }else{
                next()
            }
        },
        meta: {
            menuname: "dockerControl"
        }
    },

    {
        path: 'control/linuxcontrol',
        component: linuxControl,
        name: 'linuxControl',
        beforeEnter: (to, from, next) => {
            let osType = getLocal("agent-os");
            console.log(osType);
            if(osType == windowsOs){
                next("/main/control/linuxControl")
            }else{
                next()
            }
        },
        meta: {
            menuname: "linuxControl"
        }
    },

    {
        path: 'control/windowscontrol',
        component: windowsControl,
        name: 'windowsControl',
        meta: {
            menuname: "windowsControl"
        }
    },

  
    {
        path: '*',
        redirect:'control/dockercontrol'
    }
]
let route = [
    {
        path: '/',
        component: login,
        name: 'login',
        beforeEnter: (to, from, next) => {
            _g.doBeforeLoginout();
            next();
        }
    },
    {
        path: '/main',
        name: 'main',
        component: main,
        beforeEnter: (to, from, next) => {
            if(cookie.checkCookie("eideviceToken")){
                next()
            }else{
                _g.doBeforeLoginout();
                next("/")
            }
        },
        children: childRoute,
        redirect:'/main/device/list'    
    },
    {
        path: '*',
        redirect:'/'
    }
    
]

export default route


