import {apiGet, apiPost} from "../../assets/js/baseApi";

let LinuxPkgBaseApi = "/api/linuxpkgmgr/";
let getLinuxPkgInfoApi = function(){
    return new Promise((resolve, reject) => {
        apiGet(LinuxPkgBaseApi+'getInfo').then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}


let installLinuxPkgApi = function({taskname, projectname,  version, downloadurl, tasktype, type}){
    let data = {
        taskname,
        projectname,
        version,
        downloadurl,
        tasktype,
        type
    }
    return new Promise((resolve, reject) => {
        apiPost(LinuxPkgBaseApi+'install', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let removeLinuxPkgApi = function({taskname, projectname,  version, downloadurl, tasktype, type, filename}){
    let data = {
        taskname,
        projectname,
        version,
        downloadurl,
        tasktype,
        type, 
        filename
    }
    return new Promise((resolve, reject) => {
        apiPost(LinuxPkgBaseApi+'remove', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let upgradeLinuxPkgApi = function({taskname, projectname,  version, downloadurl, tasktype, type}){
    let data = {
        taskname,
        projectname,
        version,
        downloadurl,
        tasktype,
        type
    }
    return new Promise((resolve, reject) => {
        apiPost(LinuxPkgBaseApi+'upgrade', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}
let getRepoLinuxPkgsApi = function(){
    return new Promise((resolve, reject) => {
        apiGet(LinuxPkgBaseApi+'repoLinuxPkgs').then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}
export {
    getLinuxPkgInfoApi,
    installLinuxPkgApi,
    removeLinuxPkgApi,
    upgradeLinuxPkgApi,
    getRepoLinuxPkgsApi
}


