
import {getLocal, getSession } from '../../assets/js/storage';
import {downloadServerPort} from "../../assets/js/config"

let getRepoServerApi = function(){
    return new Promise((resolve, reject) => {
        let repoServerUrl = getSession("astore-host");
        resolve(repoServerUrl);
    })   
}
//get serverip:serverport/repoRootpath
let getDownloadServerUrlApi = function(){
    return new Promise((resolve, reject) => {
        let repoServerUrl = getSession("astore-host");
        console.log(repoServerUrl)
        let hostname = repoServerUrl.slice(0, repoServerUrl.lastIndexOf(":"));
        let dowmloadServerUrl = hostname+":"+downloadServerPort;
        
        resolve(dowmloadServerUrl);
        
    })   
}

let returnDownloadServerUrlApi = function(){
    let repoServerUrl = getSession("astore-host");
    let hostname = repoServerUrl.slice(0, repoServerUrl.lastIndexOf(":"));
    let dowmloadServerUrl = hostname+":"+downloadServerPort;
    return dowmloadServerUrl
}
export {
    getRepoServerApi,
    getDownloadServerUrlApi,
    returnDownloadServerUrlApi
}


