import {apiGet, apiPost, apiDelete, apiPut} from "../../assets/js/baseApi";

let ExeBaseApi = "/api/exemgr/";
let getExeInfoApi = function(){
    return new Promise((resolve, reject) => {
        apiGet(ExeBaseApi+'getInfo').then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let getPkgInfoApi = function(){
    return new Promise((resolve, reject) => {
        apiGet(ExeBaseApi+'getPkgInfo').then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}


let installExeApi = function({taskname, projectname,  version, downloadurl, tasktype, type, tool}){
    let data = {
        taskname,
        projectname,
        version,
        downloadurl,
        tasktype,
        type, 
        tool
    }
    return new Promise((resolve, reject) => {
        apiPost(ExeBaseApi+'install', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let removeExeApi = function({taskname, projectname,  version, downloadurl, tasktype, type, tool}){
    let data = {
        taskname,
        projectname,
        version,
        downloadurl,
        tasktype,
        type, 
        tool
    }
    return new Promise((resolve, reject) => {
        apiPost(ExeBaseApi+'remove', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let upgradeExeApi = function({taskname, projectname,  version, downloadurl, tasktype, type, tool}){
    let data = {
        taskname,
        projectname,
        version,
        downloadurl,
        tasktype,
        type, 
        tool
    }
    return new Promise((resolve, reject) => {
        apiPost(ExeBaseApi+'upgrade', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}
let getRepoWinPkgsApi = function(){
    return new Promise((resolve, reject) => {
        apiGet(ExeBaseApi+'repoApps').then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}



let getRepoWinPkgsByTypeApi = function(type){
    let data = {
        type
    }
    return new Promise((resolve, reject) => {
        apiGet(ExeBaseApi+'repoAppsByType', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}
export {
    getExeInfoApi,
    installExeApi,
    removeExeApi,
    upgradeExeApi,
    getRepoWinPkgsApi,
    getRepoWinPkgsByTypeApi,
    getPkgInfoApi
}


