<template>
    <div v-show="isSingleMode" class="p-r">
        <div class="search" v-show="isShowSearchInput"> 
            <el-input size="small" class="w-250 m-l-10" v-model="keywords" :placeholder="$t('linuxPkgControl.keywordHolder')" >  
            </el-input>
            <i class="fa fa-search search-icon" aria-hidden="true"></i>
        </div>
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane name="linuxMarket">
                <span slot="label">
                    <i class="fa fa-sellsy m-r-5" aria-hidden="listLoading"></i>
                    {{$t('linuxPkgControl.pkgMarket')}}
                </span>
                <el-table
                    :data="pkgTableData.filter(data => !keywords || data.productname.toLowerCase().includes(keywords.toLowerCase()))"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading"
                    :header-cell-style="{background:'#fafafa',color:'#606266'}"
                    >

                    <el-table-column
                    label=""
                    width="100"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img src="@/assets/imgs/deb.png"  alt="no img" style="width:52px" v-if="scope.row.filetype=='deb'"/>
                                <img src="@/assets/imgs/package.png"  alt="no img" style="width:48px" v-if="scope.row.filetype=='tar'"/>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="filename"
                    :label="$t('linuxPkgControl.fileName')"
                    min-width="120"
                    >
                    </el-table-column>
                    <el-table-column
                    prop="productname"
                    :label="$t('linuxPkgControl.productName')"
                    min-width="120"
                    sortable
                    >
                    </el-table-column>
                    <el-table-column
                    prop="version"
                    :label="$t('linuxPkgControl.version')"
                    min-width="120"
                    sortable>
                    </el-table-column>
                    <el-table-column
                    prop="filetype"
                    :label="$t('linuxPkgControl.fileType')"
                    min-width="120"
                    :filters="[{ text: $t('linuxPkgControl.debPkg'), value: 'deb' }, { text: $t('linuxPkgControl.compressPkg'), value: 'tar' }]"
                    :filter-method="filterFileType"
                    filter-placement="bottom-end"
                    >
                        <template slot-scope="scope">
                            <div>
                                <span v-if="scope.row.filetype == 'deb'">
                                    {{$t('linuxPkgControl.debPkg')}}
                                </span>
                                <span v-if="scope.row.filetype == 'tar'">
                                    {{$t('linuxPkgControl.compressPkg')}}
                                </span>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="type"
                    :label="$t('linuxPkgControl.action')"
                    min-width="120"
                    :filters="[{ text: $t('linuxPkgControl.uninstall'), value: 'uninstall' }, { text: $t('linuxPkgControl.install'), value: 'install' }, { text: $t('linuxPkgControl.upgrade'), value: 'upgrade' }]"
                    :filter-method="filterType"
                    filter-placement="bottom-end"
                    >
                        <template slot-scope="scope">
                            <div>
                                <span>
                                    <el-button-group>
                                        <el-button 
                                        size="small" 
                                        type="danger" 
                                        @click="appAction('uninstall', scope.row)"
                                        v-if="scope.row.type === 'uninstall'"
                                        >
                                            {{$t('linuxPkgControl.uninstall')}}
                                        </el-button>
                                        <el-button 
                                        size="small" 
                                        type="warning" 
                                        @click="appAction('upgrade', scope.row)" 
                                        v-if="scope.row.type === 'upgrade'"
                                        >
                                            {{$t('linuxPkgControl.upgrade')}}
                                        </el-button>
                                        <el-button 
                                        size="small" 
                                        type="primary" 
                                        @click="appAction('install', scope.row)" 
                                        v-if="scope.row.type === 'install'"
                                        >
                                            {{$t('linuxPkgControl.install')}}
                                        </el-button>
                                    </el-button-group>
                                </span>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
            <el-tab-pane name="pkgStatusHistory">
                <span slot="label">
                    <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                    {{$t('linuxPkgControl.linuxManagerHistory')}}
                </span>
                <pkg-history
                ref="pkgHistory"
                @isFinished="isFinished"
                >
                </pkg-history>
            </el-tab-pane>
        </el-tabs>
    </div>
    
</template>


<script>
    import handleResponse from '@/components/restfulapi/handleResponse';
    // import {getRepoWinPkgsApi} from '../../../components/restfulapi/getRepoWinPackageApi';
    import { 
        getLinuxPkgInfoApi,
        installLinuxPkgApi,
        removeLinuxPkgApi,
        upgradeLinuxPkgApi,
        getRepoLinuxPkgsApi} from '../../../components/restfulapi/linuxPkgApi';
    import {getSession, checkSession, setSession } from '../../../assets/js/storage';
    import pkgHistory from "./history"
    import {linuxType} from "../../../assets/js/constant";
    import {returnDownloadServerUrlApi, getDownloadServerUrlApi} from "../../restfulapi/serverConfigApi"
    import {mapState} from 'vuex'
    export default{
        name: 'singlelinuxPkgControl',
        data(){
            return {
                activeName: "linuxMarket",
                pkgTableData: [],
                defaultImg: 'this.src="' + 'static/imgs/default_windows.png' + '"',

                //card
                listLoading: false,
                keywords: '',
            }
        },

        props: {
            selectedAgentId: {
                type: String,
                default: ""
            },
            isSingleMode: {
                type: Boolean,
                default: true
            },
            //selectedAgent is offline
            isOffline: {
                type: Boolean,
                default: false
            }
        },
        components: {
            pkgHistory,
        },

        methods:{
            getSensorStatus(){
                if(!this.isSingleMode) {
                    console.error("Cannot operate in a batch mode");
                    return;
                }

                let pkgManagementInfoArray = [];
                let devicePkgArray = [];
                this.listLoading = true;

                // [{\"appname\":\"gedit\",\"pkgtype\":\"debian\",\"pkgname\":\"gedit\",\"description\":\"\",\"updatetime\":\"1600914216518\",\"version\":\"3.22.0-2\",\"status\":\"created\"}]
                getLinuxPkgInfoApi().then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "success"){
                            let allPkgData = JSON.parse(res.data);
                            if(Object.prototype.toString.call(allPkgData) === "[object Array]" && allPkgData.length > 0){
                                allPkgData.forEach((val) => {
                                    let type;
                                    if(val.pkgtype == "compressed"){
                                        type = "tar";
                                    }else if(val.pkgtype == "debian"){
                                        type = "deb";
                                    }
                                    let uninstallPkgObj = {
                                        type: 'uninstall',
                                        filename: val.pkgname,
                                        productname: val.appname,
                                        version: val.version,
                                        address: val.address,
                                        filetype: type
                                    };
                                    pkgManagementInfoArray.push(uninstallPkgObj);
                                    devicePkgArray.push(uninstallPkgObj);  
                                })
                            }
                            this.getRepoPkgs(pkgManagementInfoArray, devicePkgArray);  
                        } 
                    })   
                })
            },
            // Deletes an object of a particular value in array
            removeObjInArray(originData, rem_pkg_val){
                if(Object.prototype.toString.call(originData) === "[object Array]"){
                    originData.forEach((obj_val, index) => {
                        if(obj_val.productname === rem_pkg_val){
                            originData.splice(index ,1)
                        }
                    })
                }else{
                     console.error("data is not array")
                     return;
                }
            },
            //get repo apps
            getRepoPkgs(pkgManagementInfoArray, devicePkgArray){
                if(
                    Object.prototype.toString.call(pkgManagementInfoArray) != "[object Array]" ||
                    Object.prototype.toString.call(devicePkgArray) != "[object Array]"
                ){
                    console.error("getRepoPkgs　parameter is not array")
                    return;
                }
                let token;
                let InstallPkgManagementInfo = {};
                let UpgradePkgManagementInfo = {};
                this.listLoading = true;
                getRepoLinuxPkgsApi().then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                res.data.forEach((val) => {
                                    let version = val.versionName != null ? val.versionName:"";
                                    InstallPkgManagementInfo = {
                                        type : "install", 
                                        filename: val.filename, 
                                        productname: val.productname, 
                                        version: val.version, 
                                        address: val.address,
                                        filetype: val.type
                                    };
                                    pkgManagementInfoArray.push(InstallPkgManagementInfo);
                                    devicePkgArray.forEach((deviceapp_val, index) => {
                                        if(deviceapp_val.productname == val.productname){
                                            // pop latest app info
                                            pkgManagementInfoArray.pop();
                                            devicePkgArray[index].address = val.address;
                                            if (_g.cpr_version(val.version, deviceapp_val.version)){  
                                                UpgradePkgManagementInfo = {
                                                    type : "upgrade", 
                                                    filename: val.filename, 
                                                    productname: val.productname, 
                                                    version: val.version, 
                                                    address: val.address,
                                                    filetype: val.type
                                                };
                                                    //push update app 
                                                pkgManagementInfoArray.push(UpgradePkgManagementInfo);     
                                                
                                            }
                                            
                                        }
                                    })
                                })
                            }
                            
                        }
                        this.pkgTableData = pkgManagementInfoArray.reverse();
                    })
                    
                })
            },

            actionAppSensor(tasktype, appdata){
                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("linuxPkgControl."+tasktype), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                    if (willfunc) {
                        _g.openGlobalLoading();
                        if(tasktype == "install"){
                            installLinuxPkgApi(appdata).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status == "success"){
                                        this.activeName = "pkgStatusHistory";
                                        this.$refs.pkgHistory.intervalGetPkgStatusHistory();
                                    }else{
                                        _g.handleError(res)
                                    }
                                })
                            })
                        }else if(tasktype == "upgrade"){
                            upgradeLinuxPkgApi(appdata).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status == "success"){
                                        this.activeName = "pkgStatusHistory";
                                        this.$refs.pkgHistory.intervalGetPkgStatusHistory();
                                    }else{
                                        _g.handleError(res)
                                    }
                                })
                            })
                        }else{
                            removeLinuxPkgApi(appdata).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status == "success"){
                                        this.activeName = "pkgStatusHistory";
                                        this.$refs.pkgHistory.intervalGetPkgStatusHistory();
                                    }else{
                                        _g.handleError(res)
                                    }
                                })
                            })
                        }
                        
                    }
                })
            },

            appAction(tasktype, selectedAppData){
                getDownloadServerUrlApi().then((downloadUrl) => {
                    let setsensorval, version, projectname;
                    if(tasktype === "upgrade"){
                        setsensorval = selectedAppData.address + "/" +  selectedAppData.filename;
                        projectname = selectedAppData.productname;
                    }else{
                        setsensorval = selectedAppData.address + "/" +  selectedAppData.filename;
                        projectname = selectedAppData.productname;
                    }
                    let taskname = linuxType+ (new Date()).getTime();
                    if(setsensorval.indexOf("s3") == 0){
                        setsensorval = setsensorval;
                    }else{
                        setsensorval = downloadUrl+ setsensorval;
                    }
                    let pkgdata = {
                        taskname,
                        projectname: projectname,
                        tool: selectedAppData.tool,
                        version: selectedAppData.version,
                        filename: selectedAppData.filename,
                        tasktype: tasktype, 
                        type: linuxType,
                        downloadurl: setsensorval,
                    }
                    this.actionAppSensor(tasktype, pkgdata)
                })
                
            },

            filterType(value, row){
                return row.type === value;
            },

            filterFileType(value, row){
                return row.filetype === value;
            },

            initData(){
                this.pkgTableData = [];
            },
            handleClick(tab){
                if(tab.name === "pkgStatusHistory"){
                    this.$emit("isDisabled", false)
                }else{
                    this.$emit("isDisabled", true)
                }
            },
            isFinished(){
                this.getSensorStatus();
            }
        },
        created() {
            this.getSensorStatus();
        },
        computed: {
            isShowSearchInput(){
                if(this.activeName === "linuxMarket"){
                    return true;
                }else{
                    return false;
                }
            },
            
            ...mapState({
                configStatus: "configStatus"
            }),  
        },

        watch:{
            configStatus(){
                this.initData();
                this.getSensorStatus();
            }
        },
    }
</script>

<style lang="scss" scoped>
    .noData{
        margin-top: 30px;
        p{
            img{
                width: 27px;
                vertical-align: middle;
            }
            margin-bottom: 20px;
            color: #bfbfbf;
            font-size: 21px;
        }
    }

    .search{
        position: absolute;
        top: 3px;
        right: 15px;
        width: 230px;
        z-index: 1001;
        .search-icon{
            position: absolute;
            top: 6px;
            right: 1px;
            color: #ddd;
            z-index: 10001;
        }
    }
</style>



