<template>
    <div>
        <el-table
            :data="tableList"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="selectItem"
            :default-sort = "{prop: 'ts', order: 'descending'}"
            ref="pkgStatusHistoryTable"
            v-loading="listLoading" 
            :header-cell-style="{background:'#fafafa',color:'#606266'}"
            >
            <el-table-column
            type="selection"
            min-width="50"
            :selectable="checkSelectable">
            </el-table-column>

            <el-table-column
            prop="projectname"
            :label="$t('winPkgControl.projectName')"
            min-width="140"
            >
            </el-table-column>

            <el-table-column
            prop="version"
            :label="$t('winPkgControl.version')"
            min-width="100"
            >
            </el-table-column>
            
            <el-table-column
            :label="$t('winPkgControl.beginTime')"
            min-width="130"
            prop="ts"
                >
                <template slot-scope="scope">
                    {{scope.row.ts|time}}
                </template>
            </el-table-column>

            <el-table-column
            prop="tasktype"
            :label="$t('winPkgControl.function')"
            min-width="120"
            >
            </el-table-column>
            <el-table-column
            prop="status"
            :label="$t('winPkgControl.installStatus')"
            min-width="140"
            >
                 <template slot-scope="scope">
                    <img src="@/assets/imgs/loading1.gif" alt="" style="width:50px;height:10px;"v-if="scope.row.status==0">
                    <span v-if="scope.row.status==2" class="badge bg-green">{{$t('global.success')}}</span>
                    <span v-if="scope.row.status==1" class="badge bg-DimGray">{{$t('global.failed')}}</span>
                    <!-- {{scope.row.upstatus|AppStatus}} -->
                </template>
            </el-table-column>

            <el-table-column
            :label="$t('winPkgControl.errorStatus')"
            min-width="100"
            >
                <template slot-scope="scope">
                    {{(""+scope.row.errorcode)|appErrorStatus}}
                </template>
            </el-table-column>

            <el-table-column
            :label="$t('winPkgControl.action')"
            min-width="175"
            align="center">
                <template slot-scope="scope">
                    <div>
                        <el-button-group>
                            <!-- :disabled="scope.row.status==0" -->
                            <el-button 
                                size="mini" 
                                type="danger"
                                @click="deleteHistory(scope.row.taskname)"
                                >
                                    {{$t('global.delete')}}
                            </el-button>
                        </el-button-group>
                    </div>
                </template>
            </el-table-column>

        </el-table>

        <btn-group 
        :selectedData="multipleSelection" 
        :isDelete="true" 
        :isMove="false" 
        tid="taskname" 
        :delApi="batchDeleteStatusHistory" 
        @result="isSuccess" 
        v-show="isShowBtnGroup"
        class="fl m-t-10 cf">
        </btn-group>
        <el-button 
        v-loading="clearLoading" 
        size="small" 
        @click="clearAll()" 
        type="primary" 
        class="fl m-t-10 cf m-l-10"
            v-show="isShowBtnGroup">
        {{$t('winPkgControl.clearAll')}}
        </el-button>

        <div class="m-t-10 cf fr">
            <el-pagination
            @current-change="handleCurrentChange"
            layout="prev, pager, next"
            :page-size="limit"
            :current-page="currentPage"
            :total="dataCount"
            v-show="isshow"
            background
            >
            </el-pagination>
            
        </div> 
        
       
    </div>
</template>
<script>

    import handleResponse from '@/components/restfulapi/handleResponse'
    import{
        getInstallStatusApi,
        deleteInstallStatusHistoryApi,
        batchDeleteInstallStatusHistoryApi,
        clearStatusApi
    } from "../../restfulapi/installStatusApi"
    import btnGroup from "../../../common/btn-group";
    import {dockerType, winExeType} from "../../../assets/js/constant";

export default {
    name: 'winPkgControlHistory',
    data() {
        return {

            tableData: [],
            tableList: [],
            isshow: false,
            limit: 10,
            dataCount: 0,
            currentPage: 1,
            multipleSelection: [],
            isShowBtnGroup: false,
            clearLoading: false,
            batchDeleteStatusHistory:  batchDeleteInstallStatusHistoryApi,
            intervalTime: 7000,

            isInterval: false,
            listLoading: false,

        }
    },

    components: {
        btnGroup,
    },

    methods: {
        selectItem(val) {
            this.multipleSelection = val;
        },

        checkSelectable(row) {
            return !row.status==0;
        },

        handleCurrentChange(currentPage){
            if(currentPage){
                this.currentPage = currentPage;
            }else{
                this.currentPage = 1
            }
            this.tableList = this.tableData.slice((this.currentPage-1)*this.limit, this.currentPage*this.limit)
            this.intervalGetPkgStatusHistory();
        },

        deleteHistory(taskname){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    this.listLoading = true;
                    deleteInstallStatusHistoryApi(taskname).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => {
                            if(res.status === "success"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.intervalGetPkgStatusHistory();
                                })
                            }else{
                                _g.handleError(res);
                            }
                        })
                    })
                }
            }) 
        },
        getInstallStatusHistory(){
            if(this.isInterval === false){
                this.listLoading = true;
            }
           
           getInstallStatusApi(winExeType).then((data) => {
                this.listLoading = false;
                handleResponse(data, (res) => {
                    this.assignAppStatusData(res);
                })
            })
        
        },

        assignAppStatusData(res){
            if(Object.prototype.toString.call(res)  === '[object Object]'&& Object.prototype.toString.call(JSON.parse(res.data)) === '[object Array]'){
                let tableData = JSON.parse(res.data);
                this.tableData = tableData;
                this.tableList = tableData.slice((this.currentPage-1)*this.limit, this.currentPage*this.limit)
                this.dataCount = tableData.length;
                console.log("length:"+this.dataCount);
                this.isshow = this.dataCount > this.limit;
                this.isShowBtnGroup = this.dataCount > 0;
                let isFinish = true;

                tableData.forEach((val) => {
                    if(val.status == 0){
                        isFinish = false;
                    }
                })
                if(isFinish){
                    this.$emit("isFinished");
                    window.clearInterval(_g.timer);
                    _g.timer = null;
                }
            }else{
                window.clearInterval(_g.timer);
                _g.timer = null;
            }
        },

        intervalGetPkgStatusHistory(){
            this.getInstallStatusHistory();
            if(_g.timer === null){
                _g.timer = window.setInterval(() => {
                    this.getInstallStatusHistory();
                    this.isInterval = true;
                },this.intervalTime)
            }
            
        },


        //delete appstatus $emit
        isSuccess(result){
            if(result){
                this.intervalGetPkgStatusHistory();
            }
        },     

        initData(){
            this.tableList = [];
            this.isShowBtnGroup= false;
            this.isshow= false;
            this.isInterval = false;
        },

        clearAll(){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.clearAll'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    this.clearLoading = true;
                    clearStatusApi(winExeType).then((data) => {
                        this.clearLoading = false;
                        handleResponse((data), (res) => {
                            if(res.status === "success"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.intervalGetPkgStatusHistory();
                                })
                            }else{
                                _g.handleError(res);
                            }
                        })
                    })
                    
                }
            })

        },


    },

    created(){
        this.intervalGetPkgStatusHistory();
    },

    beforeDestroy() {
         window.clearInterval(_g.timer);
        _g.timer = null;
    },
        
}
</script>
<style lang="scss" scoped>
@import "@/assets/css/colors.scss";
    .badge{
        display: inline-block;
        min-width: 10px;
        padding: 5px 8px;
        font-size: 12px;
        font-weight: 700;
        line-height: 1;
        color: #fff;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        border-radius: 10px;
    }

    .el-loading-mask{
        top: 60px !important;
    }


    
</style>



